.Banner {
  position: relative;
  width: 100%;
  height: 100%;

  background-color: #1e1e1e;
  display: flex;
  flex-direction: column;
}

.Header {
  position: relative;
  width: 100%;
  height: 130px;

  display: flex;
  justify-content: center;
  color: white;
}

.Logo {
  flex: 1;

  display: flex;
  align-items: center;
  padding-left: 10%;
  font-size: 1.5rem;
  font-family: "Caviar Dreams";
}

.Menu {
  flex: 2;

  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 10%;
}

.Menu ul {
  list-style-type: none;
  display: flex;
}

.Menu ul li {
  margin: 0 10px;
  font-size: 1.2rem;
  font-family: "Caviar Dreams";
  display: flex;

  align-items: center;
  cursor: pointer;
}

.Menu ul li a {
  position: relative;
  color: white;
  text-decoration: none;
  display: flex;
  justify-content: center;
}

.Menu ul li a::before {
  content: "";
  position: absolute;
  width: 0;
  height: 1px;

  bottom: 0;
  background-color: white;

  transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
}

.Menu ul li a:hover::before {
  width: 100%;
}


/*-------------Biography-----------*/


.Biography{
  position: relative;
  flex: 1;

  display: flex;
}


.Content{
  flex: 1;

  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 10%;
}

.TitleContent{
  max-width: 900px;
  font-size: 5rem;
  font-family: "MuseoSansRounded-5";
  color: white;

}

.TextBiography{
  max-width: 550px;
  font-family: "Caviar dreams";
  font-size: 1.1rem;
  color: white;
}


.CheckButton{
  margin: 30px 0;
  padding-bottom: 50px;
}


.CheckButton button{
  padding: 20px 40px;
  font-family: "Caviar Dreams";
  font-size: 1.5rem;

  background-color: transparent;
  color: white;
  border: 2px solid white;
  cursor: pointer;

  transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;

}

.CheckButton button:hover{
  border-radius: 10px;
}





@media screen and (max-width:700px){
  .Menu{
    display: none;
  }

  .Logo{
    font-size: 2rem;
  }

  .TitleContent{
    font-size: 3rem;
    margin: 0 0 20px;
  }
}